import { BpmnModel } from "../BpmnModel";
import { BpmnModelElement } from "../BpmnModelElement";
import { ExtensionElement } from "../ExtensionElement";
import { ExtensionMethod } from "../ExtensionMethod";

export abstract class Participant extends ExtensionElement {
    id: string;
    name: string;

    constructor(model: BpmnModel) {
        super(model);
    }

    public ConvertToJsonObject() {
        const obj = super.ConvertToJsonObject();
        ExtensionMethod.SetAttributeValue(obj, "Id", this.id);
        ExtensionMethod.SetAttributeValue(obj, "Name", this.name);
        return obj;
    }

    public LoadFromJsonObject(obj: {}) {
        super.LoadFromJsonObject(obj);

        this.id = ExtensionMethod.GetAttributeValue4String(obj, "Id");
        this.name = ExtensionMethod.GetAttributeValue4String(obj, "Name");
    }

    clone(): Participant {
        const c = this.NewInstance() as Participant;

        c.id = this.id;
        c.name = this.name;

        return c;
    }
}